/*
    SWITCH
*/

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  /* Hide default HTML checkbox */
  /* The slider */
}

.switch input {
  display: none;
}

.switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: white;
  border: 1px solid #a2a2a2;
}

.switch .slider:hover {
  background-color: white;
}

.switch .slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: transform 300ms ease-out;
  -moz-transition: transform 300ms ease-out;
  -ms-transition: transform 300ms ease-out;
  -o-transition: transform 300ms ease-out;
  transition: transform 300ms ease-out;
}

.switch input:checked + .slider {
  background-color: #145A93;
}

.switch input:focus + .slider {
  box-shadow: none;
}

.switch input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

.switch.round .slider {
  border-radius: 20px;
}

.switch.round .slider:before {
  border-radius: 50%;
}

/*
    SVG CHECKBOX
*/

.checkbox-svg label {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.checkbox-svg label svg {
  width: 2em;
  stroke: #30B08F;
  stroke-width: 5;
  fill: transparent;
}

.checkbox-svg label svg .box {
  stroke-dasharray: 320;
  stroke-dashoffset: 0;
  transition: stroke-dashoffset 300ms linear;
}

.checkbox-svg label svg .check {
  stroke-width: 8;
  stroke-dasharray: 70;
  stroke-dashoffset: 70;
  transition: stroke-dashoffset 300ms linear;
}

.checkbox-svg label span {
  margin-left: 7px;
  font-size: 16px;
}

.checkbox-svg label input {
  display: none;
  pointer-events: none;
}

.checkbox-svg label input:checked ~ svg .box {
  stroke-dashoffset: 320;
  transition: stroke-dashoffset 300ms linear;
}

.checkbox-svg label input:checked ~ svg .check {
  stroke-dashoffset: 0;
  transition: stroke-dashoffset 300ms linear;
}

